import { useState } from "react";
import { Button } from "@/components/ui/button";
import { motion } from "framer-motion";
import { Spotify } from "lucide-react";

export default function Home() {
  const [gradient, setGradient] = useState("from-green-400 to-blue-500");

  const handleGradientChange = () => {
    const gradients = [
      "from-green-400 to-blue-500",
      "from-purple-400 to-pink-500",
      "from-yellow-400 to-red-500",
    ];
    setGradient(gradients[Math.floor(Math.random() * gradients.length)]);
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-900 text-white p-6">
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="text-center"
      >
        <Spotify size={60} className="text-green-400 mb-4" />
        <h1 className="text-5xl font-bold mb-4">Discover Your Spotify Era</h1>
        <p className="text-lg max-w-xl mx-auto text-gray-300">
          Music defines moments. Explore your unique Spotify listening trends
          and uncover the soundtrack of your life.
        </p>
      </motion.div>
      
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.4, duration: 0.8 }}
        className="mt-6"
      >
        <Button
          onClick={handleGradientChange}
          className={`px-6 py-3 text-lg font-medium rounded-2xl transition-all bg-gradient-to-r ${gradient} hover:scale-105`}
        >
          Analyze Your Music
        </Button>
      </motion.div>
    </div>
  );
}
